
.field-group {
  position: relative;
  width: 100%;
  min-height: 60px;
}
.m-choose-project {
  display: inline-block;
  width: 150px;
  position: absolute;
  right: 20px;
  bottom:0;
}
.m-api-change-page {
  position: absolute;
  width: 300px;
  min-width: 300px;
  margin: 0 -150px;
  left: 50%;
  bottom: 0;
  text-align: center;
  border-radius: 5px;
}
.m-api-title {
  width: 100%;
}
.m-api-list {
  display: inline-block;
  min-width: 350px;
  max-width: 500px;
  max-height: 400px; 
  overflow: hidden;
  margin: 20px;
  text-align: justify;
}
.m-api-card-list {
  width: 100%;
  display: block;
  text-align: center;
  padding-bottom: 60px;
}
.m-api-move-module {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 1);
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  z-index: 10;
}
.m-api-move-module-box {
  display: inline-block;
}
.m-api-move-api, .m-api-move-project {
  width: 300px;
  height: 400px;
  overflow: auto;
  display: inline-block;
  margin: 20px 30px;
  text-align: justify;
  /*border: 1px solid #fefefe;*/
}



