
.m-main-card-list {
  width: 100%;
  display: block;
  text-align: center;
}
.m-log-rightBox {
  position: absolute;
  right: 20px;
  top: 100px;
  width: 35%;
  height: calc( 100% - 150px );
  overflow-y: auto;
  overflow-x: hidden;
}
.m-log-leftBox {
  /*padding-right: 400px;*/
  width: 60%;
  padding-left: 30px;
  height: calc( 100% - 30px );
  overflow-y: auto;
  overflow-x: hidden;
}
.m-log-leftBox-tool{
  height: 150px;
  position: relative;
}
.m-log-leftBox-main {
  max-height: calc( 100% - 180px );
  overflow-y: auto;
  padding: 10px 20px;
}
.m-log-main {
  max-height: 100%;
  overflow: hidden;
}
.m-log-leftBox-main div {
  padding: 3px;
}
.m-log-leftBox-main div span:first-child {
  font-size: 12px;
}
.m-log-item {
  cursor: pointer;
}
.m-log-tool-choose {
  display: block;
  padding: 20px 10px;
}
.m-log-proj {
  width: 200px;
  display: inline-block;
}
.m-log-type {
  display: block;
  position: absolute;
  right: 30px;
  top: 40px;
}
pre {
  width: 100%;
  overflow-x: auto;
}




